<template>
    <div class="wrap" ref="wrap">
        <transition name="move">
        
        <div class="top" v-show="isShow">
            <div class="share" v-show="!isPC" @click="() => goShare()">
                <img src="../assets/img/share.png" alt="">
                <p>分享</p>
            </div>
            <div class="voice" @click.prevent="stopVoice" >
                <img src="../assets/img/voice.png" alt="">
                <p>声音：
                    <span v-show="isOpenAudio">开</span>
                    <span v-show="!isOpenAudio">关</span>
                </p>
            </div>
            <div class="point" @click="() => showSellPoint()">
                <img src="../assets/img/point.png" alt="">
                <p>车辆亮点</p>
            </div>

            <div class="size" @click="() => showSize()">
                <img src="../assets/img/canshu.png" alt="">
                <p>显示参数</p>
            </div>

        </div>
        </transition>

        <div :class="isShow?'moreInfoA':'moreInfo'">
            <div class="moreInfoIN" @click="() => goMore()">
                <img src="../assets/img/moreInfo.png" alt="">
                <p>更多</p>
            </div>

        </div>
    </div>
</template>
<script>

import {
  include
} from '@/lib/HYUNDAI-APP' ;

const isPC = include('is.pc')() ;

export default {
    data(){
        return {
            isShow:false,
            isOpenAudio: true,
            isPC
        }
    },
    methods:{
        goMore(){
            this.isShow = !this.isShow
        },

        stopVoice(){
            this.isOpenAudio = !this.isOpenAudio
            this.$emit('stopVoice')
        },
        showSellPoint () {
            this.$emit('showSellPoint')
        },
        showSize () {
            this.$emit('showSize')
        },
        goShare() {
            this.$emit('isShare')
        }
    },
};
</script>
<style lang="scss" scoped>

@include phone {
  .moreInfo{
      width:58px !important;
      height:60px !important;
      > .moreInfoIN{
          width:inherit !important;
          height:inherit !important;
          > img{
            width:34px !important;
            height:34px !important;
          }
          > p{
              font-size: 9px !important;
          }
      }
  }
  .moreInfoA{
    width:58px !important;
    height:60px !important;
    >.moreInfoIN{
        width:inherit !important;
        height:inherit !important;
    }
    img{
        width:34px !important;
        height:34px !important;
    }
    p{
        font-size: 9px !important;
    }
  }
  .top{
    > *{
        margin-top:25px;
    }
    padding:0 !important;
    height:60px !important;
    img{
        width:34px !important;
        height:34px !important;
    }
    p{
        margin-top:1px !important;
        font-size: 9px !important;
    }
  }
}
.wrap {
  position: absolute;
  top: 0;
  right: 0;
  height: 140px;

  .top {
    float: left;
    box-sizing: border-box;
    padding: 32px 0;
    width: 300px;
    height: 140px;
    color: white;
    display: flex;
    justify-content: space-between;
    text-align: center;
    .share,
    .voice,
    .point,
    .size {
      float: left;
      width: 80px;
      img {
        width: 60px;
        height: 49px;
      }
      p {
        font-size: 18px;
        margin-top: 10px;
      }
    }
  }
  .moreInfo,.moreInfoA {
    float: left;
    width: 150px;
    height: 140px;
    color: white;
    text-align: center;
    .moreInfoIN {
      float: right;
      width: 90px;
      height: 120px;
      color: white;
      text-align: center;
      img {
        width: 90px;
        height: 90px;
      }
    }
    img {
      width: 90px;
      height: 90px;
    }
  }
  .moreInfoA{
    background: url("../assets/img/moreBg.png");
    background-size: 100% 100%;
  }
}
.move-enter-active,
.move-leave-active {
  transition: all .5s;
}
.move-enter,
.move-leave-to {
  opacity: 0;
  transform: translateX(100%);
}
</style>



