<template>
    <div class="wrap">
        <ul>
            <li class="icon" @click="() => showVR()">
                <img src="../assets/img/vr.png" alt="">
                <p>VR观车</p>
            </li>
            <li class="line">
                <img src="../assets/img/long.png" alt="">
            </li>
            <li class="icon" @click="() => toExterior()">
                <img src="../assets/img/car.png" alt="">
                <p>外观视角</p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    props: [ 'frameWindow', 'exteriorConfig', 'interiorConfig', 'isShowIn' ],
    methods:{
        goDet(item,index){
            if(index%2 === 1) return
            this.$emit('Innmove',item.text)
        },
        showVR () {

            if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
             
                this.frameWindow.FontreGL.toggleVR()
                this.$emit('showVr')
            } else {
                alert(`请使用手机端打开此链接体验VR模式`)
                return 
            }
        },
        switchInnerView () {
            
            if(this.currInColorIndex === this.inColors.length - 1){
                this.currInColorIndex = 0
            }else{
                this.currInColorIndex++
            }
            this.interiorConfig.interiorColorName = this.inColors[this.currInColorIndex].enName
            this.frameWindow.FontreGL.showInterior(this.interiorConfig);
            track_event("lafesta_interior", "pick", this.interiorConfig.interiorColorName, 1)
        },
        toExterior () {
            this.frameWindow.FontreGL.showExterior(this.exteriorConfig);
            this.$emit('Innmove')
        }
    },
    data(){
        return {
            currInColorIndex: 0,
            inColors: [
                {
                    enName: 'scene_frontRow',
                    cnName: '内饰前排'
                },
                {
                    enName: 'scene_BackRow',
                    cnName: '内饰后排'
                }
            ],
            list:[
                {
                    text:"VR观车",
                    img:require('../assets/img/vr.png')
                },
                {
                    text:'',
                    img:require('../assets/img/long.png')
                },
                {
                    text:"视角切换",
                    img:require('../assets/img/change.png')
                },
                 {
                    text:'',
                    img:require('../assets/img/long.png')
                },
                {
                    text:"外观视角",
                    img:require('../assets/img/car.png')
                }
            ]
        }
    }
}
</script>
<style lang="scss" scoped>
@include phone{
    .wrap{
        height:73px !important;
        li{
            height: 73px !important;
            width:45px !important;
            &.line{
                width:20px !important;
                img{
                    width:inherit !important;
                }
            }
        }
        img{
            width:34px !important;
            height:34px !important;
        }
        p{
            font-size: 9px !important;
        }
    }
}
.wrap{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 150px;
    background: url('../assets/img/bg.png');
    ul{
        display: flex;
        justify-content: center;
        .icon{
            box-sizing: border-box;
            padding: 18px 0;
            width: 97px;
            height: 150px;
            color: white;
            text-align: center;
            img{
                width: 94px;
                height: 94px;
            }
        }
        .line{
            width: 51px;
            padding: 10px 0;
            img{
                width: 100%;
            }
        }
    }
}
</style>



