<template>
    <div class="wrap">
        <div class="local">
            <div class="title">
                选择特约店
                <div class="back" @click="goBack">
                    <img src="../assets/img/x.png" alt="">
                </div>
            </div>
            <div class="pro">
                <div>
                    <select name="" id="" v-model="selPro">
                        
                        <option value="" disabled hidden selected>请选择省份</option>
                        <option :value="pro.province" v-for="(pro,index) in Province" :key="index">{{pro.province}}</option>
                    </select>
                </div>
            </div>
            <div class="city">
                <div>
                    <select name="" id="" v-model="selCity">
                        <option value="" disabled hidden selected>请选择城市</option>

                        <option :value="city.city" v-for="(city,index) in cityList" :key="index">{{city.city}}</option>
                    </select>
                </div>
            </div>
            <div class="shop">
                <div>
                    <select name="" id="" v-model="selShop">
                        <option value="" disabled hidden selected>请选择特约店</option>
                        <option :value="shop.dealer" v-for="(shop,index) in areaList" :key="index">{{shop.dealer}}</option>
                    </select>
                </div>
            </div>
        </div>
        <div class="time">
            <div class="title">
                选择预约试驾时间
            </div>
            <input type="date" placeholder="请选择日期" v-model="time">
        </div>
        <div class="info">
            <div class="title">
                您的姓名
            </div>
            <input type="text" placeholder="请输入真实姓名" v-model="signName">
            <select name="" id="" v-model="sex">
                <option value="先生" selected>先生</option>
                <option value="女士">女士</option>
            </select>
        </div>
        <div class="tel">
            <div class="title">
                联系方式
            </div>
            <input type="text" placeholder="请输入正确的手机号码" v-model="tel" @blur="checkTel">
        </div>
        <div class="foot">
            <div class="text">
                <span :class="isCheck?'check':''" class="icon"  @click=" () => this.toCheck()"></span>
                <span class="word">已阅读并接受</span>
                <a href="https://www.beijing-hyundai.com.cn/Template/privacyPolicy.html" target="_blank">《个人信息保护声明》</a>
            </div>
            <p>温馨提示：请确保试驾当日携带有效驾照</p>
            <div class="submit"  @click=" () => this.toSubmit()">
                提交预约
            </div>
        </div>
    </div>
</template>
<script>

import { Province } from '../assets/js/pro'
import { City } from '../assets/js/city'
import { District } from '../assets/js/area'
import axios from 'axios'

const BaseUrl = 'https://h5.fontre.com/hyundai-api',
			PCUrl =  'https://bj2.vcyber.com:444/Carsupport/ajaxdrive',      // 现代 pc 端接口
			MUrl 	= 'https://bj2.vcyber.com:444/wap/Carsupport/exclusive5'   // 现代 手机端接口


export default {

	mounted(){
			// this.Province = Province
			// console.log(Province)
			this.getPro()

			this.getMedia()
	},
		
  methods: {

			getMedia() {
				let url = window.location.href
				let a = url.split('?')[1].split('&')
				for ( let i of a ) {
					let arr = i.split('=')
					if (arr.length > 0) {
						for ( let j of arr) {
							if (j === 'media') {
								this.media = arr[1]
							}
						}
					}
				}
			},

      getPro(){
          axios.get(`${BaseUrl}/listPorviceAndCity`,{
              params:{
                  filed:"province"
              }
          }).then((res) =>{
              this.Province = res.data.data
          })
			},
			
      async getCity(){
          let result
          await axios.get(`${BaseUrl}/listPorviceAndCity`,{
              params:{
                  filed:"city",
                  province:this.selPro

              }
          }).then((res) =>{
              result = res.data.data
          })
          return result
			},
			
      async getShop(){
          let result
          await axios.get(`${BaseUrl}/listDealer`,{
              params:{
                  provice:this.selPro,
                  city:this.selCity

              }
          }).then((res) =>{
              result = res.data.data
					})
					console.log(result)
          return result
			},
			
      checkTel(){
          if(!(/^1[2345678]\d{9}$/.test(this.tel))){ 
            alert("手机号码有误，请重填");  
            return false; 
        } 
      },
      toCheck(){
          this.isCheck = !this.isCheck
			},
			
      toSubmit(){

          if(!this.isCheck){
              alert('请同意条款')
              return
					}
					
          if(!!this.selShop&&!!this.signName&&!!this.tel&&!!this.time&&!!this.selPro&&!!this.selPro){

              let body = {
									car: 'lefesta',
									dealer_sn: this.selShop,
									drive_time: '',
									name: this.signName,
									gender: '',
									mobile: this.tel,
									media: this.media,
                 
							}
							track_event("lafesta_appointment", "pick", "submit", 1)

							let resUrl
							if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
								// mobile
								resUrl = MUrl
							} else {
								//pc 
								resUrl = PCUrl
							}
							 
						console.log(body)			
							// axios.post(resUrl,body).then((res) =>{
							// 	if(res.data.status == 200){
							// 			alert('预约成功')
							// 	}
							// })
							
          }else{
              alert('请完善表单')
          }
      },
      /**
       * 隐藏该组件
       */
      goBack () {
        this.$emit('handleDrive')
      }
	},
	
  watch:{
      async selPro(newValue){
          this.cityList = []
          this.areaList = []
          
          if(newValue != ""){
           
            this.cityList = await this.getCity()
          }
      },
       async selCity(newValue){
          this.areaList = []
          if(newValue != ""){
            this.areaList = await this.getShop()
          }
      }
  },
  data() {
    return {
			car: '',        // 当前车型
			dealer_sn: '',  // 经销商
			drive_time: '',  // 时间
			signName: '',       // 名字
			gender: '',     //性别
			tel: '',     //手机
			media: '',  //media

        Province:[],
        selPro:'',
        cityList:[],
        selCity:'',
        areaList:[],
        selShop:'',
        time:'',
        sex:'先生',
        isCheck:false
    };
  }
};
</script>
<style lang="scss" scoped>


@include phone{
    .wrap{
        width:231px !important;
        height:359px !important;
        left:50% !important;
        margin-left:-115.5px !important;
        top:50% !important;
        margin-top:-219.5px !important;
        >:not(:last-child){
            padding:5px !important;
        }
    }
    .title{
        font-size: 12px !important;
        position: relative !important;
        .back {
            position: absolute !important;
            top: -6px !important;
            right: 0 !important;
            img{
                width: 16px !important;
                height: 16px !important;
            }
        }
    }
    .pro{
        select{
            width:100px !important;
            height:24px !important;
        }
    }
    .city{
        select{
            width:100px !important;
            height:24px !important;
        }
    }
    .shop{
        select{
            width:210px !important;
            height:24px !important;
        }
    }
    .time{
        input{
            width:199px !important;
            height:24px !important;
        }
    }
    .info{
        input{
            width:90px !important;
            height:15px !important;
        }
        select{
            width:99px !important;
            height:24px !important;
        }
    }
    .tel{
        input{
            width:199px !important;
            height:24px !important;
        }
    }
    .foot{
        padding:5px !important;
        span.icon{
            width:10px !important;
            height:10px !important;
        }
        span,a{
            font-size: 9px !important;
        }
        p{
            font-size: 8px !important;
        }
        .submit{
            margin-top:11px !important;
            font-size: 15px !important;
            width:205px !important;
            height:35px !important;
            line-height: 35px !important;
        }
    }
}
.wrap {
  select {
    background: rgba(255, 255, 255, 0.4);
    border: 2px solid rgba(255, 255, 255, 1);
    color: #CCCCCC;
  }
  option{
      border-bottom: 1px solid #EEEEEE;
     background:rgba(0,0,0,0.8); 
  }
  input{
      color: white;
      padding-left: 8px;
      &::placeholder{
          color: #CCCCCC;
      }
  }
  position: absolute;
  width: 440px;
  right: 90px;
  top: 44px;
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #00448d;
  box-shadow: 0 0 6px black;
  padding-bottom: 10px;
  .title {
    font-size: 20px;
    color: white;
    margin: 6px 0;

    position: relative;
    .back {
        position: absolute;
        top: -6px;
        right: 0;
        img{
            width: 20px;
            height: 20px;
        }
    }
  }
  .local {
    box-sizing: border-box;
    padding: 10px;
    .pro,
    .city {
      float: left;
      select {
        width: 200px;
        height: 44px;
      }
    }
    .pro {
      margin-right: 10px;
    }
    .shop {
      select {
        margin-top: 10px;

        width: 410px;
        height: 44px;
      }
    }
  }
  .time {
    box-sizing: border-box;
    padding: 10px;
    input {
      width: 410px;
      height: 44px;
      background: rgba(255, 255, 255, 0.4);
      border: 2px solid rgba(255, 255, 255, 1);
    }
  }
  .info{
     box-sizing: border-box;
    padding: 10px; 
     input {
      width: 240px;
      height: 44px;
      background: rgba(255, 255, 255, 0.4);
      border: 2px solid rgba(255, 255, 255, 1);
    }
    select {
        width: 160px;
        height: 48px;
      }
  }
  .tel{
     box-sizing: border-box;
    padding: 10px;  
    input {
      width: 410px;
      height: 44px;
      background: rgba(255, 255, 255, 0.4);
      border: 2px solid rgba(255, 255, 255, 1);
    }
  }
  .foot{
       box-sizing: border-box;
    padding: 10px;  
      .text{
          .icon{
              width: 20px;
              height: 20px;
              display: inline-block;
              background: url('../assets/img/check.png');
              background-size: 100% 100%;
              vertical-align: sub;
          }
          .icon.check{
             background: url('../assets/img/isCheck.png');
              background-size: 100% 100%; 
          }
          .word{
              color: white;
              font-size: 18px;
          }
          a{
              font-size: 18px;
              color: #0A4C93;
          }
      }
      p{
          margin-top: 8px;
          font-size: 16px;
          color: #999999;
      }
      .submit{
          margin-top: 22px;
          width: 410px;
          height: 60px;
          background: #0A4C93;
          text-align: center;
          line-height: 60px;
          color: white;
          font-size: 24px;
          cursor: pointer;
      }
  }
}
</style>




