<template>

  <div class="wrap" v-show="isModal">

    <div class="back" @click="() =>back()">
      <img src="../assets/img/x.png" alt="">
    </div>

    <div class="box">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in list" :key="index">
            <img :src="item.img" alt="">
          </div>
        </div>
      </div>

      <div class="nav left" @click="() => goPre()">
        <img src="../assets/img/left.png" alt="">
        <!-- <p>上一个</p>  -->
      </div>

      <div class="nav right" @click="() => goNext()">
        <img src="../assets/img/right.png" alt="">
        <!-- <p>下一个</p> -->
      </div>

      <div class="swiper-pagination"></div>
    </div>

  </div>
</template>

<script>
import swiper from "swiper";
export default {
  data() {
    return {
      isModal: false,
      currentItem: {
        name: "",
        img: "",
        title: "",
        desc: ""
      },
      swiper: null,
      list: [
        {
          name: "spark_tip_02",
          img: require("../assets/img/details/qiangeshan.png"),
          title: "镀铬蜂窝式前格删",
          desc: ""
        },
        {
          name: "roof",
          img: require("../assets/img/details/houshijing.png"),
          title: "全黑车顶",
          desc: ""
        },
        {
          name: "body",
          img: require("../assets/img/details/cheshensheji.png"),
          title: "背溜式车身",
          desc: ""
        },
        {
          name: "vent",
          img: require("../assets/img/details/paiqiguan.png"),
          title: "动感十足双排孔",
          desc: ""
        },
        
        {
          name: "eng",
          img: require("../assets/img/details/fadongji.png"),
          title: "发动机",
          desc: ""
        },
        {
          name: "light",
          img: require("../assets/img/details/qiandadeng.png"),
          title: "精锐LED前大灯",
          desc: ""
        },
        {
          name: "tail",
          img: require("../assets/img/details/houweiyi.png"),
          title: "运动型后尾翼",
          desc: ""
        }
      ]
    };
  },
  mounted() {
    this.swiper = new swiper(".swiper-container", {
      loop: true,
      observer: true, //修改swiper自己或子元素时，自动初始化swiper
      observeParents: true, //修改swiper的父元素时，自动初始化swiper
      paginationType: 'fraction',
      pagination: '.swiper-pagination',
      onInit(s) {
        console.log(s)
      }
    })
  },
  methods: {
    goPre(){
      this.swiper[0].slidePrev()
    },

    goNext(){
      this.swiper[0].slideNext()
    },

    show(el) {
      this.isModal = true;
    },

    back() {
      this.isModal = false;
      this.currentItem = {};
    }
  }
};
</script>
<style lang="scss" scoped>

.wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
 
  
  .back {
    position: absolute;
    right: 40px;
    top: 48px;
    width: 45px;
    height: 45px;
    cursor: pointer;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .box {
    width: 40%;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%,-50%);
    .nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      z-index: 999;
      p{
        color: #fff;
      }
    }
    .left {
      right: 100%;
    }
    .right {
      left: 100%;
    }
  }

  .swiper-container {
    z-index: 2;
    width: 100%;
    height: 100%;
    .swiper-slide {
    }
    img {
      width: 100%;
    }
  }
  .swiper-pagination {
    color: #fff;
    bottom: -10px;
  }

  .text {
    position: relative;
    box-sizing: border-box;
    top: -70px;
    width: 682px;
    height: 222px;
    background: #00448d;
    background: url("../assets/img/details/ang.png");
    background-size: 100% 100%;
    padding: 70px 54px;
    margin: 0 auto;
    h4 {
      font-size: 48px;
      color: white;
      margin-left: 50px;
    }
    p {
      font-size: 24px;
      color: white;
      text-indent: 2em;
      margin-top: 20px;
    }
  }
}


// 手机端
@media screen and (max-width:560px){ 

.wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
 
  .back {
    position: absolute;
    right: 15px;
    top: 15px;
    width: 25px;
    height: 25px;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .box {
    position: absolute;
    left: 50%;
    top: 40%;
    width: 90%;
    transform: translate(-50%,-50%);
    
    .nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 999;
      display: none;
    }
    .left {
      left: 0;
    }
    .right {
      right: 0;
    }
  }
}
}

</style>



