<template>
    <div class="wrap">
        <ul>
            <li v-for="(item,index) in list" :key="index" :class="index%2 === 1?'line':'icon'" @click="() => goDet(item,index)" >
                <div class="li_selected" 
                    :class="{
                        'selected_spec': index%2 === 0 && (index === 0 || index === list.length -3),
                        'selected_spec2': index%2 === 0 && index === 2
                        }"
                    v-show="index%2 === 0 && index !== list.length-1  && currentSelected === index"></div>
                <img :src="item.img" alt="">
                <p>{{item.text}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {

    mounted(){
       let lis =  this.$el.querySelectorAll('li.line')
       lis[0].setAttribute('class','longLine')
       lis[lis.length-1].setAttribute('class','longLine')
    },

    methods:{
        goDet(item,index){
            if(index%2 === 1) return
            this.currentSelected = index
            this.$emit('move',item.text)
        }
    },
    data(){
        return {
            currentSelected: null,
            list:[
                {
                    text:"切换场景",
                    img:require('../assets/img/scene.png')
                },
                {
                    text:'',
                    img:require('../assets/img/long.png')
                },
                {
                    text:"外观颜色",
                    img:require('../assets/img/out.png')
                },
                {
                    text:'',
                    img:require('../assets/img/short.png')
                },
                {
                    text:"格珊",
                    img:require('../assets/img/lan.png')
                },
                {
                    text:'',
                    img:require('../assets/img/short.png')
                },
                {
                    text:"轮毂",
                    img:require('../assets/img/wheel.png')
                },
                {
                    text:'',
                    img:require('../assets/img/short.png')
                },
                {
                    text:"装饰",
                    img:require('../assets/img/opt.png')
                },
                // {
                //     text:'',
                //     img:require('../assets/img/long.png')
                // },
                // {
                //     text:"参数",
                //     img:require('../assets/img/canshu.png')
                // },
                {
                    text:'',
                    img:require('../assets/img/long.png')
                },
                {
                    text:"内饰视角",
                    img:require('../assets/img/seat.png')
                },
            ]
        }
    }
}
</script>
<style lang="scss" scoped>

@include phone{
    .wrap{
        height:75px !important;
        li{
            .li_selected{
                width:50px !important;
            }
            width:40px !important;
            img{
                width:40px !important;
                height:40px !important;
            }
            &.longLine,&.line{
                width:20px !important;
                img{
                    width:20px !important;
                }
            }
            p{
                font-size: 9px !important;
            }
        }
    }
}

.wrap{
    position: absolute;
    left: 0;
    bottom: 15px;
    width: 100%;
    height: 150px;
    background: url('../assets/img/bg.png') no-repeat;
    background-size: 100%;
    // filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../assets/img/bg.png', sizingMethod='scale');
    // -ms-filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../assets/img/bg.png', sizingMethod='scale');
    // background-size: cover;
    // -moz-background-size: cover;
    // -webkit-background-size: cover;
    ul{
        display: flex;
        justify-content: center;
        // display: table;
        // margin: 0 auto;
        li{
            .li_selected{
                position: absolute;
                top: 0;
                left: -20px;
                background: #0E4F95;
                height: 100%;
                width: 134px;
                z-index: -2;
                opacity: 0.7;
                transform: skew(-20deg);
                -webkit-transform: skew(-20deg);
            }
            .selected_spec{
                width: 142px;
            }
            .selected_spec2{
                left: -24px;
                width: 139px;
            }
        }
         .icon{
            box-sizing: border-box;
            padding: 18px 0;
            width: 97px;
            height: 150px;
            color: white;
            text-align: center;
            position: relative;
            z-index: 2;
            img{
                width: 94px;
                height: 94px;
            }
        }
        .line{
            width: 40px;
            // height: 130px;
            padding: 20px 0;
            img{
                width: 100%;
            }
        }
         .longLine{
            width: 51px;
            // height: 130px;
            padding: 10px 0;
            img{
                width: 100%;
            }
        }

    }
    .mask{
        position: absolute;
        left: 0;
        top: 0;
        background: #000;
        width: 100px;
        height: 100px;
    }
}

</style>



