const designWidth = 1920;

export default {

    layout(state){

        let {
            isPC
        } = state ;

        if(isPC){

            let scale = state.scale = innerWidth / designWidth ;

            state.height = `${innerHeight / scale}px` ;
        
        }else{

            state.height = innerHeight ;
        }
    }

} ;


