<template>
  <div class="wrap" v-show="isModal">
    <div class="back" @click="() => back()">
      <img src="../assets/img/x.png" alt="">
    </div>


    <div class="box" v-show='currentShow==="spark_tip_01"'>

      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item) in video2" :key="item.title" :style="{backgroundImage: 'url('+ item.iUrl +') '}">
              <video :src="item.vUrl" controls ></video>
              <div class='title'>{{item.title}}</div>
          </div>
        </div>
      </div>

      <div class="nav left" @click="() => goPre()">
        <img src="../assets/img/left.png" alt="">
        <!-- <p>上一个</p> -->
      </div>

      <div class="nav right" @click="() => goNext()">
        <img src="../assets/img/right.png" alt="">
        <!-- <p>下一个</p> -->
      </div>

      <div class="swiper-pagination"></div>

    </div>


    <div class="box"  v-show='currentShow==="1"'>

      <div class="swiper-container">
        <div class="swiper-wrapper">

          <div class="swiper-slide video-height" v-for="(item,idx) in video3" :key="idx" :style="{backgroundImage: 'url('+ item.iUrl +') '}">
            <video :src="item.vUrl" controls webkit-playsinline="true"></video>
            <div class='title'>{{item.title}}</div>
          </div>

          <div class="swiper-slide" >
            <img :src="require('../assets/img/video/zhinengwanglian.png')" alt="">
          </div>
          <div class="swiper-slide" >
            <img :src="require('../assets/img/video/duomeitixitong.png')" alt="">
          </div>
        </div>
      </div>

      <div class="nav left" @click="goPre">
        <img src="../assets/img/left.png" alt="">
        <!-- <p>上一个</p> -->
      </div>

      <div class="nav right" @click="goNext">
        <img src="../assets/img/right.png" alt="">
        <!-- <p>下一个</p> -->
      </div>

      <div class="swiper-pagination"></div>

    </div>
  
  </div>
</template>
<script>

import swiper from "swiper";

export default {
  data() {
    return {
      isModal: false,
      videoH: 0,
      currentItem: {
        name: "",
        img: "",
        title: "",
        desc: ""
      },
      swiper:null,

      video2: [
        {
          vUrl: require('../assets/video/fuzhuxitong.mp4'),
          iUrl: require('../assets/img/video/fuzhuxitong.png'),
          title: 'LKA车道保持辅助系统',
        },
        {
          vUrl: require('../assets/video/zhidongxitong.mp4'),
          iUrl: require('../assets/img/video/zhidongxitong.png'),
          title: 'FCA前碰撞预警制动系统(可识别行人)',
        },
        {
          vUrl: require('../assets/video/zishiyingxunhang.mp4'),
          iUrl: require('../assets/img/video/zishiyingxunhang.png'),
          title: 'ASCC智能自适应巡航(带启停功能)',
        },
        // {
        //   vUrl: require('../assets/video/jinguangtiaojie.mp4'),
        //   iUrl: require('../assets/img/video/jinguangtiaojie.png'),
        //   title: 'HBA智能远近光调节'
        // }
      ],

      video3:[
        {
          vUrl: require('../assets/video/xianshixitong.mp4'),
          iUrl: require('../assets/img/video/xianshixitong.png'),
          title: 'RVM后方影像显示系统'
        }
      ],
      currentShow: null
    };
  },

  mounted() {
    this.swiper = new swiper(".swiper-container", {
      loop: true,
      observer: true, //修改swiper自己或子元素时，自动初始化swiper
      observeParents: true, //修改swiper的父元素时，自动初始化swiper
      paginationType: 'fraction',
      pagination: '.swiper-pagination',
    });

  },
  methods: {
    goPre() {
      if ( this.currentShow==="1" ) {
        this.swiper[2].slidePrev()
      } else {
        this.swiper[1].slidePrev()
      }
    },

    goNext() {

      if ( this.currentShow==="1" ) {
        this.swiper[2].slideNext()
      } else {
        this.swiper[1].slideNext()
      }
    },

    show(el) {
      this.currentShow = el

      if(el === 'spark_tip_01' ) {
        this.list = this.video2
        this.isModal = true;
      }

      if(el === 'spark_tip_02') {
        this.isModal = true;
        this.list = this.list3
      }

      if(el === '1') {
        this.isModal = true;
        // this.list = this.video3
      }

      if(el === '2') {
        this.isModal = true;
      }
    },

    back() {
      this.isModal = false;
      // this.currentItem = {};
    }
  }
};
</script>
<style lang="scss" scoped>

video{
  border: 1px solid transparent;
  width: 100%;
}

.back {
  position: absolute;
  right: 40px;
  top: 48px;
  width: 45px;
  height: 45px;
  cursor: pointer;
  img {
    width: 100%;
    height: 100%;
  }
}

.wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;

  .box {
    box-sizing: border-box;
    width: 50%;
    // height: 530px;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%,-50%);
    .nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      p{
        color: #fff;
      }
    }
    .left {
      right: 100%;
    }
    .right {
      left: 100%;
    }
  }

  .swiper-container {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    z-index: 2;
    .swiper-slide {
      padding: 15px 25px 70px 18px;
      box-sizing: border-box;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      .title {
        display: none;
      }
    }
    img {
      width: 100%;
      height: 100%;
    }
  }

  .swiper-pagination {
    color: #fff;
    bottom: -10px;
  }

}


// 手机端
@media screen and (max-width:560px){ 

.back {
  position: absolute;
  right: 15px;
  top: 15px;
  width: 25px;
  height: 25px;
  img {
    width: 100%;
    height: 100%;
  }
}
.wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;

  .box {
    box-sizing: border-box;
    width: 100%;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%,-50%);
    .nav {
      display: none;
    }
  }

  .swiper-container {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    z-index: 2;
    .swiper-slide {
      position: relative;
      padding: 0;
      box-sizing: border-box;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      .title {
        display: block;
        padding: 10px;
        color: #fff;
        background: #154b81;
      }
    }
    img {
      width: 100%;
      height: 100%;
    }
  }

}
}
</style>



