<template>
    <div class="wrap">
        <ul v-show="showOut">
            <li v-for="(item,index) in list1" :key="index" @click="() => changeExColor(item)">
                <img :src="item.img" alt="" :class="currentColor === item.eng ?'active':''">
                <p :class="currentColor === item.eng ?'activeP':''">{{item.text}}</p>
                <img src="../assets/img/iconBg.png" alt="" class="tan" v-show="currentColor === item.eng">
            </li>
        </ul>
         <ul v-show="showLan">
            <li v-for="(item,index) in list2" :key="index" @click="() => changeGrating(item)">
                 <img :src="item.img" alt="" :class="currentLan === item.eng ?'active':''">
                <p :class="currentLan === item.eng ?'activeP':''">{{item.text}}</p>
                <img src="../assets/img/iconBg.png" alt="" class="tan" v-show="currentLan === item.eng">
            </li>
        </ul>
         <ul v-show="showWheel">
            <li v-for="(item,index) in list3" :key="index" @click="() => changeHub(item)">
                 <img :src="item.img" alt="" :class="currentWheel === item.eng ?'active':''">
                <p :class="currentWheel === item.eng ?'activeP':''">{{item.text}}</p>
                <img src="../assets/img/iconBg.png" alt="" class="tan" v-show="currentWheel === item.eng">
            </li>
        </ul>
        <ul v-show="showView">
            <li v-for="(item,index) in list4" :key="index" @click="() => changeSence(item)">
                 <img :src="item.img" alt="" :class="currentSence === item.eng ?'active':''">
                <p :class="currentSence === item.eng ?'activeP':''">{{item.text}}</p>
                <img src="../assets/img/iconBg.png" alt="" class="tan" v-show="currentSence === item.eng">
            </li>
        </ul>
        <ul v-show="showOrn">
            <li v-for="(item,index) in list5" :key="index" @click="() => changeOrn(item)">
                <img :src="item.img" alt="" :class="currentOrn === item.eng ?'active':''">
                <p :class="currentOrn === item.eng ?'activeP':''">{{item.text}}</p>
                <img src="../assets/img/iconBg.png" alt="" class="tan" v-show="currentOrn === item.eng">
            </li>
            <li  @click="() => changeBlock(item6)">
                <img :src="item6.img" alt="" :class="currentBlock === item6.eng ?'active':''">
                <p :class="currentBlock === item6.eng ?'activeP':''">{{item6.text}}</p>
                <img src="../assets/img/iconBg.png" alt="" class="tan" v-show="currentBlock === item6.eng">
            </li>
        </ul>
    </div>
</template>
<script>

export default {
    data(){
        return {
            currentColor:'blue',
            currentLan:'carGrill1',
            currentWheel:'hub1',
            currentSence:'view1',
            currentOrn:'',
            currentBlock: '',
            list1:[
                {   
                    eng:'white',
                    text:'珍珠白',
                    img:require('../assets/img/white.png')
                },
                 {  
                    eng:'silver',
                    text:'古月银',
                    img:require('../assets/img/silver.png')
                },
                 {
                    eng:'gray',                    
                    text:'潘特灰',
                    img:require('../assets/img/gray.png')
                },
                 {
                    eng:'red',                    
                    text:'炽热红',
                    img:require('../assets/img/red.png')
                },
                 {
                    eng:'blue',                    
                    text:'昊玥蓝',
                    img:require('../assets/img/blue.png')
                },
                 {
                    eng:'gold',                    
                    text:'沙漠金',
                    img:require('../assets/img/yellow.png')
                },
                 {
                    eng:'black',                    
                    text:'爵士黑',
                    img:require('../assets/img/black.png')
                },
            ],
            list2:[
                {   
                    eng:'carGrill2',
                    text:'金属质感横幅式',
                    img:require('../assets/img/lan1.png')
                },
                 {  
                    eng:'carGrill1',
                    text:'镀铬蜂窝式',
                    img:require('../assets/img/lan2.png')
                },
            ],
            list3:[
                {   
                    eng:'hub1',
                    text:'18寸轮毂',
                    img:require('../assets/img/wheel1.png')
                },
                {
                    eng:'hub3',                    
                    text:'17寸轮毂',
                    img:require('../assets/img/wheel3.png')
                },
                {  
                    eng:'hub2',
                    text:'16寸轮毂',
                    img:require('../assets/img/wheel2.png')
                }
            ],
            list4:[
            {   
                eng:'view1',
                text:'场景1',
                img:require('../assets/img/s1.png')
            },
                {  
                eng:'view2',
                text:'场景2',
                img:require('../assets/img/s2.png')
            },
            {  
                eng:'view3',
                text:'场景3',
                img:require('../assets/img/s3.png')
            },
            {  
                eng:'view4',
                text:'场景4',
                img:require('../assets/img/s4.png')
            },
            ],
            list5:[
                {   
                    eng:'applique1',
                    text:'贴花1',
                    img:require('../assets/img/orn1.png')
                },
                {
                    eng:'applique2',                    
                    text:'贴花2',
                    img:require('../assets/img/orn3.png')
                },
                {
                    eng:'applique3',                    
                    text:'贴花3',
                    img:require('../assets/img/orn4.png')
                }
             ],
             item6: {  
                eng:'fender',
                text:'挡泥板',
                img:require('../assets/img/orn2.png')
            }
        }
    },
    props:{
        showOut:{
            default:false
        },
        showView:{
            default:false
        },
        showLan:{
            default:false
        },
        showWheel:{
            default:false
        },
        showOrn:{
            default:false
        },
        frameWindow:{
            require:true
        },
        exteriorConfig:{
            require:true
        },
        interiorConfig:{
            require:true
        }
    },
    methods:{
        changeExColor (param) {
            this.exteriorConfig.exteriorColorName = param.eng
            this.currentColor = param.eng
            this.frameWindow.FontreGL.showExterior(this.exteriorConfig);
            track_event("lafesta_color", "pick", param.eng, 1)
        },
        changeGrating (param) {
            this.exteriorConfig.configLineName = param.eng
            this.currentLan = param.eng
            this.frameWindow.FontreGL.showExterior(this.exteriorConfig);
            track_event("lafesta_grille", "pick", param.eng, 1)
        },
        changeHub (param) {
            this.exteriorConfig.hubName = param.eng
            this.currentWheel = param.eng
            this.frameWindow.FontreGL.showExterior(this.exteriorConfig);
            track_event("lafesta_hub", "pick", param.eng, 1)
        },
        changeSence(param) {
            console.log(param)
            this.currentSence = param.eng
            this.frameWindow.FontreGL.changeScene(this.currentSence);
            this.frameWindow.FontreGL.wheelAnimation({
                bool: false
            });
            this.$emit('currentSence',this.currentSence)
            track_event("lafesta_scene", "pick", param.eng, 1)
        },
        changeOrn(param) {
            if(this.currentOrn === param.eng){
                this.currentOrn = ''
                this.exteriorConfig.configLineName = `no_${param.eng}`
            }else{
                this.currentOrn = param.eng
                this.exteriorConfig.configLineName = param.eng
            }
            this.frameWindow.FontreGL.showExterior(this.exteriorConfig);
            track_event("lafesta_decorative", "pick", param.eng, 1)
        },
        changeBlock(param) {
            if(this.currentBlock === param.eng){
                this.currentBlock = ''
                this.exteriorConfig.configLineName = `no_${param.eng}`
            }else{
                this.currentBlock = param.eng
                this.exteriorConfig.configLineName = param.eng
            }
            this.frameWindow.FontreGL.showExterior(this.exteriorConfig);
            track_event("lafesta_decorative", "pick", param.eng, 1)
        }
    }
}
</script>
<style lang="scss" scoped>

@include phone{
    
    .wrap{
        height:43px !important;
        li{
            width:46px !important;
            height:43px !important;
            padding:0 !important;
            img{
                &.active{
                    top:4px !important;
                    left:1px !important;
                }
                width:46px !important;
                height:20px !important;
            }
            p{
                &.activeP{
                    top:24px !important;
                }
                margin-top:2px !important;
                font-size: 9px !important;
            }
        }
    }
}

.wrap{
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 0;
    width: 100%;
    height: 110px;
    // background: url('../assets/img/debg.png');
    // background-size: 100% 100%;

    background: url('../assets/img/debg.png') no-repeat;
    filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../assets/img/debg.png', sizingMethod='scale');
    -ms-filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../assets/img/debg.png', sizingMethod='scale');
    background-size: cover;
    -moz-background-size: cover;
    -webkit-background-size: cover;
     ul{
        // display: flex;
        // justify-content: center;
        // overflow: hidden;
        display: table;
        margin: 0 auto; 
        li{
            float: left;
            padding: 18px;
            width: 120px;
            height: 110px;
            color: white;
            text-align: center;
            position: relative;
           
            .tan{
                position: absolute;
                left: 0;
                top: 4px;
                width: 175px;
            }
            p{
                line-height: 20px;
                font-size: 18px;
                margin-top: 10px;
            }
            img{
                width: 120px;
            }
            .active{
                position: absolute;
                left: 14px;
                top: 12px;
                width: 150px;
                z-index: 10;
            }
            .activeP{
                position: absolute;
                top: 85px;
                left: 0;
                right: 0;
                margin: 0 auto;
            }
        }
    }
}
</style>



