<template>
  <div id="app" :style="{
    transform:`scale(${scale})`,
    '-ms-transform':`scale(${scale})`,
    height
  }" :class="{
     pc:isPC
  }">
    <div class="hengping" v-show='isHorizontal'>
      <div class='text'>请使用竖屏模式观看体验！</div>
    </div>
    <router-view/>
  </div>
</template>

<script>

import {
  mapState
} from 'vuex' ;

export default {
  name: 'App',

  computed:mapState([
    'scale',
    'height',
    'isPC'
  ]),
  data() {
    return {
      isHorizontal: false
    }
  },

  mounted(){

    let {
      $store,
      isPC
    } = this ;

    $store.commit('layout') ;

    if(isPC){

      window.addEventListener('resize' , () =>{
        $store.commit('layout') ;
      });
    }
    this.updateOrientation()
    window.onorientationchange = this.updateOrientation;
  },

  methods: {
    updateOrientation() {
      if (window.orientation === 180 || window.orientation === 0) {
          // 竖屏状态 do something
          console.log('竖屏')
          this.isHorizontal = false
      }
      if(window.orientation === 90||window.orientation === -90) {
          // 横屏状态 do something
          console.log('横屏')
          this.isHorizontal = true
      }
    }
  }

}
</script>

<style lang="scss">

  * {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  html, body{
    position: relative;
    height: 100%;
    /* margin: 0;
    padding: 0; */
    overflow: hidden;
  }

  #app {
    &.pc{
      width:1920px;
    }
    transform-origin:left top ;
    /* height: 100%;
    margin: 0;
    padding: 0; */
  }
  .hengping{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: black;
    color: #fff;
    z-index: 999999;
    font-size: 30px;
    text-align: center;
    // display: table-cell; 
    .text {
      margin-top: 25%;
    }
}
</style>



