<template>
  <div class="container">
    <audio :src='require("../assets/video/back.mp3")' loop autoplay />
    <div class="right-mask1" v-show="!isShowInterior">
      <!-- <img src="../assets/img/shodow2.png" alt=""> -->
    </div>

    <Loading :processNum='processNum' :processFlag="processFlag"/>
    <iframe frameborder="0" width='100%' :height='height' scrolling='no' src="webgl.html"></iframe>


    <div class="moreIn" v-show="!isShowVr && showWebglCar">
      <MoreInfo @stopVoice="stopVoice" @showSellPoint = "showSellPoint" @showSize='showSize' @isShare='isShare'></MoreInfo>
    </div>
    <div class="sideBar" v-show="!isShowInterior && showWebglCar">
      <div class="drive" @click="() => handleDrive()">
        <img src="../assets/img/drive.png" alt="">
        <p>预约试驾</p>
      </div>
      <div class="like" @click="() => handleLike()">
        <img src="../assets/img/like.png" alt="">
        <p>留言赞赏</p>
      </div>
      <div class="light" @click="() => openLight()">
        <img src="../assets/img/light.png" alt="">
        <p>车灯开启</p>
      </div>
      
      <div class="start" @click="() => toSport()" v-show="view === 'view1'">
        <img src="../assets/img/start.png" alt="">
        <p>{{ !isRuning? '一键启动':'一键停止' }}</p>

        <div class="stop" @click="toSport" v-show="isRuning ">
          <!-- <img src="../assets/img/stop.png" alt=""> -->
          <div class="fire" :class="mode === 'sport'?'ed':''" @click.stop="() =>changeMode('sport')">
            <img src="../assets/img/fire.png" alt="">
            <p>运动模式</p>
          </div>
          <div class="oil" :class="mode === 'oil'?'ed':''" @click.stop="() => changeMode('oil')">
            <img src="../assets/img/oil.png" alt="">
            <p>节油模式</p>
          </div>
            <div class="com" :class="mode === 'com'?'ed':''" @click.stop="() => changeMode('com')">
            <img src="../assets/img/com.png" alt="">
            <p>舒适模式</p>
          </div>
        </div>
      </div>


    </div>
    <transition name='move'>
      <div class="detail-wrap" v-show="showOut||showLan||showWheel||showView||showOrn">
        <Detail :showOut='showOut' :showLan='showLan' :showWheel='showWheel' :showView='showView' :showOrn='showOrn' :frameWindow='frameWindow' :exteriorConfig='exConfig' :interiorConfig='inConfig' @currentSence='currentSence'></Detail>
      </div>
    </transition>

    <div class="btn-layout" v-show="!isShowVr">
      <Main @move="goDet" v-show="!isShowInterior && showWebglCar"></Main>
      <Spritespin @move="goDet" v-show="!isShowInterior && !showWebglCar"></Spritespin>
      <Inner @Innmove="hideInDet" v-show="isShowInterior" :frameWindow='frameWindow' :exteriorConfig='exConfig' :interiorConfig='inConfig' @showVr='showVr'></Inner>
    </div>
    <div class="vrMode" v-show="isShowVr" @click="() => vrBack()">
      <img src="../assets/img/vrBack.png" alt="">
    </div>
    <!-- 图文信息说明 -->

    <!-- 模态框开始 -->
    <Modal ref="modal"></Modal>
    <modal2 ref="modal2"></modal2>

    <!-- 模态框结束 -->
    <!-- 留言 -->
    <transition name="right">
      <Message v-show="isMes" @handleLike="handleLike"></Message>
    </transition>

    <!-- 预约试驾 -->
    <transition name="right">
      <Drive v-show="isDrive" @handleDrive="handleDrive"></Drive>
    </transition>

    <transition name = "opa">
      <div class="share" v-show="sharing" @click="() => shareClose()">
        <img src="../assets/img/share1.png" alt="" class="shareImg">
      </div>
    </transition>
  </div>
</template>
<script>

import Loading from "../components/Loading";
import Main from "../components/Main";
import Detail from "../components/Detail";
import More from "../components/More";
import Inner from "../components/Inner";
import MoreInfo from "../components/MoreInfo";
import Modal from "../components/Modal";
import Message from "../components/Message";
import Drive from "../components/Drive";
import Modal2 from "../components/Modal2";
import Spritespin from "../components/Spritespin";

import {
  mapState
} from 'vuex' ;

import {
  include
} from '@/lib/HYUNDAI-APP' ;

export default {
  components: {
    Loading,
    Main,
    Detail,
    More,
    Inner,
    MoreInfo,
    Modal,
    Message,
    Drive,
    Modal2,
    Spritespin
  },
  computed:mapState([
    'height'
  ]),
  data() {
    return {
      sharing:false,
      showWebglCar: null,
      isShowSellPoint:false,
      isMes: false,
      isDrive: false,
      isShowVr: false,
      showOrn:false,
      showLan: false,
      showWheel: false,
      showOut: false,
      showView: false,
      currentView: "sportView",
      isOpenLight: false,
      processNum: 0,
      processFlag: 1,
      IFRAME: null,
      frameWindow: null,
      isShowInterior: false,
      isSelcet: false,
      isChangeWheel: false,
      view: "view1",
      isRuning: false,
      mode: "sport",
      exConfig: {
        carName: "lafesta",
        exteriorColorName: "blue",
        interiorColorName: "scene_frontRow",
        configLineName: "carGrill1",
        hubName: "hub1"
      },
      inConfig: {
        carName: "lafesta",
        exteriorColorName: "blue",
        interiorColorName: "scene_frontRow"
      },
      isShowSize: false,
    };
  },
  created() {
  },

  mounted() {

    let self = this;

    this.audio = document.querySelector("audio")

    self.IFRAME = document.querySelector("iframe");
    self.ifLoaded(self.IFRAME).then(iWindow => {

      self.frameWindow = iWindow;
      self.frameWindow.FontreGL.fontreBind();
      self.frameWindow.FontreGL.on(
        "loading_exterior",
        (stage, process, msg) => {
          self.processNum = process;
          self.processFlag = stage;
          if (self.processFlag === 2) {
            self.frameWindow.FontreGL.hideShade();
          }
        }
      );
      self.frameWindow.FontreGL.on("active_sell_point", name => {
        if(name === 'spark_tip_02'){
          self.$refs.modal.show(name);        
        }else{
          self.$refs.modal2.show(name);      
        }
        track_event("lafesta_sellingpoint", "pick", name, 1)    
      });
      self.frameWindow.FontreGL.on("active_to_interior_point", () => {
        self.goDet('内饰视角')
      })
    
      self.showWebglCar = self.frameWindow.FontreGL.isSupportWebgl()
      self.frameWindow.FontreGL.showExterior(self.exConfig);

      //图文信息展示
      const info ={
       state: false,
        showCarText(pram) {
            console.log(pram)
            self.$refs.modal2.show(pram); 
            track_event("lafesta_sellingpoint", "pick", pram, 1)     
          }
      }
      self.frameWindow.InteriorInfo = info
    });

  },

  methods: {
    isShare(){
      this.sharing = true
    },
    goDet(el) {
      console.log(el);

      if (el === "切换场景") {
        // let newView = this.currentView === 'sportView'?'cityView':'sportView'
        // this.changeView(newView)
        this.showView = !this.showView;
        this.showOut = false;
        this.showLan = false;
        this.showWheel = false;
        this.showOrn = false;

      } else if (el === "外观颜色") {
        this.showOut = !this.showOut;
        this.showView = false;
        this.showLan = false;
        this.showWheel = false;
        this.showOrn = false;
        console.log(this.showOut)
      } else if (el === "格珊") {
        this.showLan = !this.showLan;
        this.showView = false;
        this.showOut = false;
        this.showWheel = false;
        this.showOrn = false;

      } else if (el === "轮毂") {
        this.showWheel = !this.showWheel;
        this.showView = false;
        this.showLan = false;
        this.showOut = false;
        this.showOrn = false;

      } else if (el === "内饰视角") {
        this.goINner();
        this.showWheel = false;
        this.showView = false;
        this.showLan = false;
        this.showOut = false;
        this.showOrn = false;
      }else if (el === "装饰") {
        this.showOrn = !this.showOrn
        this.showWheel = false;
        this.showView = false;
        this.showLan = false;
        this.showOut = false;
      }else if ( el === "参数") {
        // if ( !this.isShowSize ) {
        //   this.frameWindow.FontreGL.showSize().length('4660mm')
        //   this.frameWindow.FontreGL.showSize().width('1790mm')
        //   this.frameWindow.FontreGL.showSize().height('1425mm')
        // } else {
        //   this.frameWindow.FontreGL.showSize().removeSize()
        // }
        // this.isShowSize = !this.isShowSize
        // // this.showWheel = false;
        // // this.showView = false;
        // // this.showLan = false;
        // // this.showOut = false;
      }
    },
    shareClose(){
      this.sharing = false;
    },
    goInDet(el) {
      if (el === "外观视角") {
        this.frameWindow.FontreGL.showExterior(this.exConfig);
        this.isShowInterior = false;
      }
    },
    showSize() {
        if ( !this.isShowSize ) {
          this.frameWindow.FontreGL.showSize().length('4660mm')
          this.frameWindow.FontreGL.showSize().width('1790mm')
          this.frameWindow.FontreGL.showSize().height('1425mm')
        } else {
          this.frameWindow.FontreGL.showSize().removeSize()
        }
        this.isShowSize = !this.isShowSize

    },
    showVr() {
      this.isShowVr = true;
    },
    vrBack() {
      this.frameWindow.FontreGL.toggleVR()
      this.isShowVr = false;
    },
    currentSence(el) {
      this.view = el;
    },
    hideInDet() {
      this.isShowInterior = false;
    },
    goINner() {
      this.frameWindow.FontreGL.showInterior(this.inConfig);
      this.isShowInterior = true;
    },
    stopVoice() {

      if (this.audio.paused) {
        this.audio.play()
        // alert(this.audio.paused)
      } else {
        this.audio.pause()
        // alert(this.audio.paused)
      }
    },
    changeView(name) {
      this.currentView = name;
      if (this.frameWindow) {
        this.frameWindow.FontreGL.changeView(name);
      }

      if (this.currentView !== "sportView") {
        this.frameWindow.FontreGL.stopWheelAnimation(false);
      } else {
        this.frameWindow.FontreGL.stopWheelAnimation(!this.isRuning);
      }
    },
    toStart() {
      this.frameWindow.FontreGL.toStart();
    },
    changeMode(item) {
      this.mode = item;
      this.isRuning = true;
      this.frameWindow.FontreGL.wheelAnimation({
        bool: this.isRuning,
        isFast: this.mode === 'sport',
        isFuel: this.mode === 'oil',
        isComfort: this.mode === 'com'
      });
    },
    handleLike() {
      this.isMes = !this.isMes;
      this.isDrive = false;
      track_event("lafesta_comments", "pick",this.isMes, 1)
    },
    showSellPoint(){
       this.isShowSellPoint = !this.isShowSellPoint
       this.frameWindow.FontreGL.switchSellPoint({
        bool: this.isShowSellPoint
      })
    

    },
    handleDrive() {
      this.isDrive = !this.isDrive;
      this.isMes = false;
      track_event("lafesta_appointment", "pick", this.isDrive, 1)
    },
    toSport() {
      if (this.view === "view2") return;

      this.isRuning = !this.isRuning;
      this.frameWindow.FontreGL.wheelAnimation({
        bool: this.isRuning,
        isFast: this.mode === 'sport'
      });
    },
    openLight() {
      this.isOpenLight = !this.isOpenLight;
      this.frameWindow.FontreGL.openLights({
        bool: this.isOpenLight
      });
      track_event("lafesta_light", "pick", this.isOpenLight, 1) 
    },
    ifLoaded(el) {
      return {
        then: resolve => {
          el.onload = () => resolve(el.contentWindow);
        }
      };
    },

    /**
     * 绑定陀螺仪监控
     */
    bindOrientationEvent(colorList) {
      if (window.parent.DeviceOrientationEvent) {
        const compute = event => {
          let alpha = event.gamma;

          let limits = 120;

          let colorLen = colorList.length;

          let interval = limits / colorLen;

          let starOffset = -60;

          let endOffset = 60;

          if (alpha < starOffset) {
            this.exConfig.exteriorColorName = colorList[0];
            this.frameWindow.FontreGL.showExterior(this.exConfig);
          } else if (alpha > endOffset) {
            this.exConfig.exteriorColorName = colorList[colorLen - 1];
            this.frameWindow.FontreGL.showExterior(this.exConfig);
          } else {
            let tempOffset = alpha - starOffset;
            let index = Math.floor(tempOffset / interval);
            if (index === colorLen) {
              index = colorLen - 1;
            }
            this.exConfig.exteriorColorName = colorList[index];
            this.frameWindow.FontreGL.showExterior(this.exConfig);
          }
        };
        window.parent.addEventListener("deviceorientation", compute, false);
      } else {
        alert("浏览器不支持陀螺仪");
      }
    },

    /**
     * 摇一摇监控
     */
    bindMotionEvent() {}
  }
};
</script>
<style lang="scss" scoped>


@include phone {
  .sideBar{
    top:150px !important;
    > * {
      width:58px !important;
      height:63px !important;
      &:first-child{
        // margin-bottom: 109px !important;
      }
    }
    p{
      font-size: 9px !important;
    }
    img{
      width:34px !important;
      height:34px !important;
    }
  }
  .detail-wrap{
      bottom:75px !important;
      height:43px !important;
  }

  .start>.stop{
    position: absolute;
    display: block;
    top: 100%;
    left: 0;
    img{
      width:34px !important;
      height:auto !important;
    }
    > div{
      margin:0 5px;
      width:36px !important;
      &.fire{
        top:52px !important;
      }
      &.oil{
        top:108px !important;
      }
       &.com{
        top:170px !important;
      }
    }
  }
}
.share{
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0,0,0,.6);
  width: 100%;
  height: 100%;
  .shareImg{
    position: absolute;
    top: 3%;
    right: 45px;
    width: 50%;
  }
}
.right-mask {
  position: absolute;
  right: 0;
  top: 0;
  width: 150px;
  height: 100%;
  background: url("../assets/img/shodow2.png");
  background-repeat: repeat;
}
.moreIn {
  float: right;
}
.btn-layout {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.detail-wrap {
  position: absolute;
  width: 100%;
  height: 110px;
  left: 0;
  bottom: 150px;
}
.sideBar {
  position: absolute;
  right: 0;
  top: 200px;
}
.start{
  position: relative;
}
.light,
.start,
.drive,
.like {
  width: 90px;
  height: 120px;
  color: white;
  text-align: center;
  font-size: 16px;
}
.light img,
.start img,
.drive img,
.like img {
  width: 90px;
  height: 90px;
}
.move-enter-active,
.move-leave-active {
  transition: all 0.5s;
}
.opa-enter-active,
.opa-leave-active {
  transition: all 0.5s;
}
.opa-enter,
.opa-leave-to {
  opacity: 0;
}
.move-enter,
.move-leave-to {
  opacity: 0;
  transform: translateY(100%);
}
.right-enter-active,
.right-leave-active {
  transition: all 0.5s;
}
.right-enter,
.right-leave-to {
  opacity: 0;
  transform: translateX(100%);
}
.stop {
  position: absolute;
  top: 0;
  right: 100%;
  display: flex;
  .oil,
  .fire,
  .com {
    color: white;
    font-size: 18px;
    text-align: center;
    width: 85px;
    margin: 0 5px;
  }
  img{
    width: 100%;
  }
}


.ed {
  background: url("../assets/img/eng.png");
  background-size: 100% 100%;
}
.vrMode {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 177px;
  height: 174px;
}
.vrMode img {
  width: 100%;
  height: 100%;
}

</style>






