<template>
    <div class="wrap">
        <div class="main">
            <div class="back" @click="goBack">
                <img src="../assets/img/x.png" alt="">
            </div>
            <img src="../assets/img/like/main.png" alt="">
            <div class="like" @click="() =>this.tolike()">
                <img src="../assets/img/like/in.png" alt="" v-show="hasLike">
                <img src="../assets/img/like/un.png" alt="" v-show="!hasLike">
                <span>{{num}}</span>
            </div>
        </div>
        <div class="message">
            <ul>
                <li v-for="(item,index) in list" :key="index" class="mes-box">
                    <p class="local">{{item.commentName}}</p>
                    <p class="txt">{{item.commentContent}}</p>
                    <div class="finger" @click="() => toFinger(item,index)">
                        <img src="../assets/img/unfinger.png" alt="" class="unfinger" ref="unfinger">
                        <img src="../assets/img/finger.png" alt="" class="hasfinger" ref="hasfinger">
                        <span>{{item.commentZan}}</span>
                    </div>

                </li>
            </ul>
        </div>
        <div class="box">
            <input type="text" placeholder="在此输入您想说的内容..." v-model="text">
            <div class="submit"  @click="() =>this.toSubmit()">
                发送
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
const BaseUrl = ' https://h5.fontre.com/hyundai-api'
export default {
    mounted(){
      this.getCom()
      this.getLike()  
    },
    methods:{
        async getLike() {
            let res = await axios({
                url:`${BaseUrl}/getCarZan?carId=1`,
            })
            this.num = res.data.data
        },
        async getCom(){
            let res = await axios({
                url:`${BaseUrl}/listComments?carId=1`,
            })
            this.list = res.data.data
            
        },
        tolike(){
            this.hasLike = !this.hasLike
            if(this.hasLike === true){
                axios({
                    url:`${BaseUrl}/zanForCar?CarId=1`
                }).then((res) =>{
                    if(res.data.status == 200){
                        this.getLike()
                    }
                })
            }
        },
        toFinger(item,index){
            let { hasfinger,unfinger } = this.$refs            
            if(hasfinger[index].style.display == 'block') return
            hasfinger[index].style.display = 'block'
            unfinger[index].style.display = 'none'
            axios({
                url:`${BaseUrl}/zanForComment`,
                params:{
                    commentId:item.id
                }
            }).then((res) =>{
                if(res.data.status == 200){
                    this.getCom()
                }
            })
        },
        async toSubmit(){
            if(this.text == ''){
                return
            }
            
            let res = await axios({
                    url:`${BaseUrl}/addComments`,
                    method:'get',
                    params:{
                        carId:1,
                        commentContent:this.text
                    }             
            })
            if(res.data.status == 200){
                alert('留言成功')
                this.getCom()
            }
        },
        /**
         * 隐藏该组件
         */
        goBack () {
            this.$emit('handleLike')
        }
    },
    data(){
        return {
            num:1232,
            hasLike:false,
            text:'',
            isFinger:false,
            list:[
                {
                    local:"网友",
                    ip:"1.1.1.1",
                    text:"留言留言留言留言留言留"
                },
                 {
                    local:"网友",
                    ip:"1.1.1.1",
                    text:"留言留言留言留言留言留"
                },
                 {
                    local:"网友",
                    ip:"1.1.1.1",
                    text:"留言留言留言留言留言留"
                },
                 {
                    local:"网友",
                    ip:"1.1.1.1",
                    text:"留言留言留言留言留言留"
                },
                 {
                    local:"网友",
                    ip:"1.1.1.1",
                    text:"留言留言留言留言留言留"
                },
            ]
        }
    }
}
</script>
<style lang="scss" scoped>
@include phone{
    .wrap{
        width:231px !important;
        height:398px !important;
        left:50% !important;
        margin-left:-115.5px !important;
        top:50% !important;
        margin-top:-239px !important;
        .main{
            > img{
               width:231px !important;
            }
            .like{
                right:15px !important;
                bottom:18px !important;
                img{
                    height:18px !important;
                }
                span{
                    top:3px !important;
                    left:22px !important;
                    line-height: 15px !important;
                    font-size: 9px !important;
                }
            }
            .back {
                position: absolute !important;
                top: 5px !important;
                right: 5px !important;
                img{
                    width: 16px !important;
                    height: 16px !important;
                }
            }
        }
        .message{
            height:238px !important;
            overflow: hidden;
            li{
                padding:12px !important;
            }
            p{
                &.local{
                    font-size: 7px !important;
                }
                &.txt{
                    font-size:9px !important;
                }
            }
            .finger{
                width: 40px;
                span{
                    left: 20px  !important;
                    top: 0 !important;
                    font-size: 12px !important;
                }
                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .box{
            input{
                width:165px - 20px !important;
                padding-left:9px !important;
                height:29px !important;
                font-size: 12px !important;
            }
            .submit{
                margin-top:2px;
                font-size: 15px !important;
                width:53px !important;
                height:35px !important;
                line-height: 35px !important;
            }
        }
    }

}
.wrap{
    position: absolute;
    right: 90px;
    top: 44px;
    background: rgba(0,0,0,.8);
    border: 2px solid #00448d;
    box-shadow: 0 0 6px black;
    padding-bottom: 10px;
    .main{
        position: relative;
        .like{
            position: absolute;
            bottom: 27px;
            right: 24px;
            span{
                position: absolute;
                top: 0;
                left: 52px;
                font-size: 24px;
                color: white;
                line-height: 48px;
            }
        }
        .back {
            position: absolute;
            top: 5px;
            right: 5px;
            img{
                width: 20px;
                height: 20px;
            }
        }
    }
    .message{
        max-height: 450px;
        overflow: auto;
   
            .mes-box{
                position: relative;
                padding: 20px;
                border-bottom: 2px solid transparent;
                border-image: url('../assets/img/line.png') 30 30 round;
                p{
                    color: white;
                }
                .local{
                    font-size: 14px;
                }
                .txt{
                    margin-top: 8px;
                    font-size: 18px;
                    line-height: 20px;
                }
                .finger{
                    position: absolute;
                    right: 20px;
                    top: 30%;
                    span{
                        position: absolute;
                        left: 37px;
                        top: 8px;
                        display: block;
                        color: #FFFFFF;
                        font-size: 18px;
                    }
                }
            }
        }
        .box{
            margin: 5px;
            input{
                float: left;
                width: 330px;
                height: 60px;
                font-size: 24px;
                border: 2px solid white;
                background: rgba(255,255,255,.4);
                color: #CCCCCC;
                padding-left: 18px;
                &::placeholder{
                color: #CCCCCC;
                }
            }
            .submit{
                float: right;
                width: 90px;
                height: 66px;
                background: #0A4C93;
                color: white;
                cursor: pointer;
                line-height: 70px;
                font-size: 26px;
                text-align: center;
            }
            
        }
.hasfinger{
    display: none;
}
}
</style>




