<template>
  <div class="load_wapper">
    <div class="loading_content">
        <div class="down load"></div>
        <div class="up load" :style='{width: processNum+ "%"}'></div>
    </div>
  </div>
</template>
<script>

export default {
  name: "Loading",
  props: {
    processNum: Number,
    processFlag: Number
  },
  data() {
    return {
      angle: -110
    };
  },
  watch: {
    processFlag() {
      if (this.processFlag === 2) {
         document.querySelector('.load_wapper').style.display = 'none'
      }
    }
  },
  
  mounted() {}
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

*{
    margin: 0;
    padding: none;
  }

.load_wapper {
  position: absolute;
  z-index: 1500;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
}

.loading_content{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.loading_content .load {
  height: 200px;
  width: 400px;
}


.loading_content .down {
  background: url(../assets/img/loading1.png) no-repeat;
  background-size: 100%;
}

.loading_content .up {
  position: absolute;
  left: 0;
  top: 0;
  background: url(../assets/img/loading2.png) no-repeat;
  width: 0%;
  background-size: 400px;
}

.point_show{
  position: absolute;
  top:50%;
  left:6%;
  transform: translateY(-50%);
}
.content_show{
  width:50%;
  color:#fff;
  font-weight: bold;
  text-align: center
}
.show_num{
   font-size:30px;
  margin-bottom: 10px;
}
.show_txt{
 font-size:18px;
}

// 手机端
@media screen and (max-width:560px){ 
  .loading_content .load {
    height: 200px;
    width: 200px;
  }
  .loading_content .up {
    position: absolute;
    left: 0;
    top: 0;
    background: url(../assets/img/loading2.png) no-repeat;
    width: 2%;
    background-size: 200px;
  }
}

</style>



