import Vue from 'vue'
import Router from 'vue-router'
import Loading from '@/components/Loading'
import IndexPage from '@/pages/Index'

Vue.use(Router)

export default new Router({
  routes: [

    {
      path: '/',
      name: 'Fontre Car',
      component: IndexPage
    },
    {
      path: '/loading',
      name: 'Loading',
      component: Loading
    }
  ]
})



